/* reset */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
nav ul,
nav li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

ol,
ul {
	list-style: none;
	margin: 0px;
	padding: 0px;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* start editing from here */

a {
	text-decoration: none;
}

.txt-rt {
	text-align: right;
}

/* text align right */

.txt-lt {
	text-align: left;
}

/* text align left */

.txt-center {
	text-align: center;
}

/* text align center */

.float-rt {
	float: right;
}

/* float right */

.float-lt {
	float: left;
}

/* float left */

.clear {
	clear: both;
}

/* clear float */

.pos-relative {
	position: relative;
}

/* Position Relative */

.pos-absolute {
	position: absolute;
}

/* Position Absolute */

.vertical-base {
	vertical-align: baseline;
}

/* vertical align baseline */

.vertical-top {
	vertical-align: top;
}

/* vertical align top */

nav.vertical ul li {
	display: block;
}

/* vertical menu */

nav.horizontal ul li {
	display: inline-block;
}

/* horizontal menu */

img {
	max-width: 100%;
}

/*end reset*/

body,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Times New Roman', Times, serif;
}

.bigBadBox {
	background: url(./bg.jpg) no-repeat;
	background-size: 100% 100%;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	-ms-background-size: cover;
	text-align: center;
	background-position: center;
	background-attachment: fixed;
	position:fixed;
	padding:0;
	margin:0;
	top:0;
	left:0;
	width: 100%;
	height: 100%;
}

/*--header--*/

.header h1 {
	font-size: 4em;
	color: #fff;
	font-weight: 500;
	margin-top: .8em;
	font-family: 'Pinyon Script', cursive;
	letter-spacing: 3px;
}

/*--//header--*/

/*--main--*/

.w3-main {
	width: 53%;
	margin: 3em auto;
	-webkit-box-shadow: -5px 20px 30px 8px rgba(0, 0, 0, 0.45);
	box-shadow: -5px 20px 30px 8px rgba(0, 0, 0, 0.45);
	background: rgba(87, 175, 150, 0.29);
}

.agile-info {
	padding: 3em 0;
}

.agile-info h2 {
	font-size: 14em;
	color: #fff;
	line-height: 1;
	letter-spacing: 2px;
	font-weight: 300;
	text-shadow: 0 2px 2px rgba(0, 0, 0, 0.6);
}

.agile-info h3 {
	font-size: 3em;
	color: #fff;
	letter-spacing: 3px;
	text-shadow: 0 2px 2px rgba(0, 0, 0, 0.6);
}

.agile-info p {
	font-size: .9em;
	color: #fff;
	text-transform: capitalize;
	letter-spacing: 5px;
	margin: 1em 0 4em;
	font-weight: 300;
}

i.fa.fa-angle-double-left {
	padding-right: .5em;
}

.agile-info a {
	font-size: 1em;
	text-transform: uppercase;
	color: #fff;
	padding: .7em 1.8em;
	letter-spacing: 2px;
	display: inline-block;
	background: #114477;
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-o-transition: 0.5s all;
	-moz-transition: 0.5s all;
	-ms-transition: 0.5s all;
}

.agile-info a:hover {
	background: #8899cc;
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-o-transition: 0.5s all;
	-moz-transition: 0.5s all;
	-ms-transition: 0.5s all;
}

/*--//main--*/

/*--footer--*/

.footer-w3l p {
	margin: 1.8em 0;
	color: #fff;
	font-size: 1em;
	letter-spacing: 2px;
	font-weight: 400;
}

.footer-w3l a {
	color: #fff;
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-o-transition: 0.5s all;
	-moz-transition: 0.5s all;
	-ms-transition: 0.5s all;
}

.footer-w3l a:hover {
	color: #efbf31;
	text-decoration: underline;
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-o-transition: 0.5s all;
	-moz-transition: 0.5s all;
	-ms-transition: 0.5s all;
}

.footer-w31 {
	position: fixed;
	bottom: 0;
	width: 100%;
}

/*--//footer--*/

/*--responsive--*/

