	.header h1 {
		margin-top: .5em;
	}
	.w3-main {
		width: 55%;
	}
}

@media(max-width: 1366px) {
	.agile-info h2 {
		font-size: 13em;
	}
	.agile-info h3 {
		font-size: 2.8em;
	}
}

@media(max-width: 1280px) {
	.w3-main {
		width: 59%;
	}
}

@media(max-width: 1080px) {
