		margin: 2.7em 0;
		font-size: .9em;
	}
}

@media(max-width: 600px) {}

@media(max-width: 568px) {
	.header h1 {
		font-size: 3.2em;
	}
	.w3-main {
		width: 91%;
	}
	.agile-info h2 {
		font-size: 8.5em;
	}
	.agile-info h3 {
		font-size: 2.1em;
	}
	.footer-w3l p {
		margin: 1.6em 0;
		line-height: 2.1;
	}
}

@media(max-width: 480px) {
	.header h1 {
		font-size: 2.8em;
	}
	.agile-info h2 {
		font-size: 7.5em;
	}
	.agile-info h3 {
		font-size: 1.8em;
	}
	.agile-info p {
		letter-spacing: 3px;
	}
	.footer-w3l p {
		margin: 3em 0;
	}
}

@media(max-width: 440px) {}

@media(max-width: 414px) {
	.header h1 {
		margin-top: .5em;
	}
	.agile-info h2 {
		font-size: 7em;
	}
	.agile-info p {
		letter-spacing: 2px;
	}
	.w3-main {
		margin: 4em auto;
	}
}

@media(max-width: 384px) {
	.header h1 {
		font-size: 2.4em;
	}
	.w3-main {
		margin: 3em auto;
	}
	.w3-main {
		width: 93%;
	}
	.w3-main {
		margin: 2.5em auto;
	}
	.footer-w3l p {
		font-size: .8em;
	}
}

@media(max-width: 375px) {}

@media(max-width: 320px) {
	.header h1 {
		font-size: 2.2em;
		letter-spacing: 1px;
		margin-top: .5em;
	}
	.agile-info h2 {
		font-size: 6em;
	}
	.agile-info h3 {
		font-size: 1.5em;
	}
	.agile-info p {
		letter-spacing: 1px;
		line-height: 2;
		margin: 1em 0 2.5em;
	}
	.agile-info a {
		font-size: .8em;
		letter-spacing: 1px;
		padding: .8em 1.8em;
	}
	.footer-w3l p {
		font-size: .75em;
		margin: 2em 0;
	}
	.w3-main {
		margin: 1.5em auto;
	}
}

/*--//responsive--*/


